@use 'base/variables' as v;
@use 'base/mixins' as m;
.botonAmarillo{
    @include m.boton(v.$amarillo, block);
}
.boton-verde,
    input.boton-verde {
    @include m.boton(v.$verde, block);
    border: none;
    cursor: pointer;
}


.botonAmarillo2{
    @include m.boton(v.$amarillo, inline-block);
}