@use 'base/variables' as v;
@use 'base/mixins' as m;
.contenedor-anuncios{
    margin: 0 2rem;
    @include m.tablet{
       @include m.grid(3)
    }
    margin: 2rem 0;
}
.anuncio{
    background-color: v.$gris;
    border: 1px solid v.$grisOscuro;
    margin-bottom: 2rem;
    @include m.tablet{
               max-width: 35rem;
    }
        a{
            font-size: 2.2rem;
        }
}
.contenido-anuncio{
    

    padding: 2rem;
    h3,p{
        margin: 0;
    }

}
.iconos-caracteristicas{
    @include m.grid(3);

    li{
        list-style: none;
        display: flex;
        align-items: center;
        img{
            width: 3rem;
            height: 4rem;
            margin-right: 1rem;
        }
        p{
            font-weight: bold;
            font-size: 3rem;
        }
    }
}
.botonAmarillo{
    @include m.boton(v.$amarillo, block);
}
.boton-verde{
    @include m.boton(v.$verde, block);

}
.precio{
        color:v.$verde;
        font-size: 3rem;
        font-weight: bold;
    }